
.main-body{
    background-color: transparent;
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 0px 20px;
    padding-top: 40px;
}



.home__section{
    display: flex;
    flex-direction: column;
    gap: 15px;

}

.suggested-grid{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    align-items: center;
    gap: 7px;
}

.fa-home{
    color: white;
}

.grid-item{
    height: 45px;
    width: 100%;
    background-color: var(--main-gray);
    border-radius: 3px;
    display: flex;
    align-items: center;
    gap: 7px;
    padding-right: 5px;
    cursor: pointer;
}

.setting-icon{
    color: lightgray;
    transform: scale(1.05);
    cursor: pointer;
}

.setting-section{
    align-self: flex-end;
}

.section-header{
    color: white;
    font-size: 18px;

}

.suggested-item__pic{
    height: 100%;
}


.suggested-item__name{
    color: white;
    font-size: 10px;
    font-weight: 500;
    overflow: hidden;
    max-height: 42px;
}

.scroll-container{
    display: flex;
    overflow-x: scroll;
    width: 100%;
    height: 120px;
    gap: 10px;
    max-width: 320px;
}

.recently-played__item{
    height: 100%;
    width: 100px;
    min-width: 100px;
}

.new-releases__item{
    height: 100%;
    width: 130px;
    min-width: 130px;
    background-color: green;
    cursor: pointer;
    display: flex;
    flex-direction: column;

}

.new-releases{
    height: 130px;
}

.new-releases__item-pic{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.recently-played__item-img{
    width: 100px;
}

.recently-played__item-name{
    color: white;
    font-size: 10px;
}